@import url(https://fonts.googleapis.com/css?family=Noto+Sans+TC:100,300,400,500,700,900&display=swap);
.header {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	width: 100%;
	height: 10vh;
	-webkit-align-items: center;
	        align-items: center;
	padding: 5px;
	position: fixed;
	top: 0;
	background-color: #fff;
	z-index: 50;
}

.header-name {
	margin-left: 5%;
}

.header-name a:first-child {
	font-size: 25px;
	font: normal normal bold 25px/1.4em poppins-semibold, poppins, sans-serif;
}

.header-name::before {
	content: '';
	width: 16px;
	height: 16px;
	background-color: #0050ff;
	position: absolute;
	left: 4%;
	top: 5vh;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}

.header-name a {
	color: #000000;
	text-decoration: none;
	font-size: large;
	font-weight: bold;
}

.header-name span {
	margin: 0 5px;
}

.nav {
	display: -webkit-flex;
	display: flex;
}

.nav a {
	margin-right: 12px;
	list-style: none;
	color: #000000;
	text-decoration: none;
	font-family: 'Open Sans', sans-serif;
}

.nav a.active {
	color: #0050ff;
}

.nav a:hover {
	color: #0050ff;
}

.profile-container {
	width: 415px;
	height: 550px;
	background-color: #f4ece6;
	position: absolute;
	right: -10%;
	top: 50%;
	-webkit-transform: translateY(-40%);
	        transform: translateY(-40%);
	box-shadow: -11.31px 11.31px 25px 0px rgba(138, 131, 124, 0.23);
	border-radius: 5px;
	z-index: 1;
}

.profile-content {
	position: absolute;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	top: 10%;
	text-align: center;
}

.profile-content img {
	border-radius: 50%;
	width: 205px;
	height: 205px;
}

.profile-name {
	line-height: 1.2em;
	font-size: 30px;
	position: relative;
}

.profile-content span {
	letter-spacing: 0.25em;
	display: inline-block;
	/* width: 220px; */
	margin-top: 30%;
}

.profile-content span::before {
	content: '';
	width: 50px;
	height: 5px;
	background-color: #0050ff;
	position: absolute;
	left: 50%;
	bottom: 15%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
}

.social {
	width: 100%;
	background-color: #fff;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	position: absolute;
	bottom: 0;
}

.social i {
	color: #000;
	font-size: 25px;
	margin: 15px;
	cursor: pointer;
}

.introduce-container {
	position: absolute;
	top: 24%;
	left: 10%;
}

.introduce-container p:first-child {
	font-size: 100px;
	font: normal normal bold 100px/1.4em poppins-semibold, poppins, sans-serif;
}

.introduce-container p:nth-child(2) {
	font-size: 25px;
}

.introduce-content {
	min-height: 175px;
	width: 369px;
	font-size: 17px;
	line-height: 2em;
}

.introduce-container a {
	display: inline-block;
	background-color: #0050ff;
	text-decoration: none;
	text-align: center;
	color: #fff;
	border-radius: 15px;
	width: 35%;
	margin: 10% 0;
	padding: 5px;
	font-size: 15px;
	font-weight: bold;
}

.introduce-container a:hover {
	background-color: #fff;
	color: #000;
	border: 2px solid #0050ff;
}

.introduce-container a + a {
	background-color: #fff;
	color: #000;
	border: 1.5px solid #000;
	margin-left: 10px;
}

.introduce-container a + a:hover {
	background-color: #0050ff;
	color: #fff;
	border: 1.5px solid #0050ff;
}

.about-container {
	display: -webkit-flex;
	display: flex;
	height: 100vh;
}

.about-container .left-bg {
	background-color: #e6dace;
	-webkit-flex: 3 1;
	        flex: 3 1;
	position: relative;
}

.about-container .right-bg {
	background-color: #fff;
	-webkit-flex: 4 1;
	        flex: 4 1;
	position: relative;
}

.professional {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	background-color: #fff;
	width: 724px;
	min-height: 450px;
	position: relative;
	margin: 2.5% 0;
	box-shadow: -11.31px 11.31px 25px 0px rgba(138, 131, 124, 0.23);
}

.professional-content {
	width: 100%;
}

.professional-content h2 {
	margin-left: 7%;
}

.professional-info {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-content: flex-start;
	        align-content: flex-start;
	width: 60%;
	height: 100%;
	padding-top: 2%;
	padding-left: 7%;
	margin: 10px 0;
}

.professional-info p {
	width: 35%;
	margin: 5%;
	position: relative;
}

.professional-info p::before {
	content: '';
	width: 12px;
	height: 12px;
	background-color: #0050ff;
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	left: -15%;
}

.resume-container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	background-color: #e6dace;
	width: 100%;
	margin-top: 10vh;
	padding: 1%;
	position: relative;
}

.resume-container h1 {
	font: normal normal bold 35px/1.4em poppins-semibold, poppins, sans-serif;
}

.resume-container h1::before {
	content: '';
	width: 16px;
	height: 16px;
	background-color: #0050ff;
	position: absolute;
	left: 43%;
	top: 2%;
	-webkit-transform: translateY(-65%);
	        transform: translateY(-65%);
}

.resume-container h2 {
	font: normal normal bold 25px/1.4em poppins-semibold, poppins, sans-serif;
	margin-top: 4%;
}

.histroy {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	background-color: #fff;
	width: 724px;
	min-height: 368px;
	position: relative;
	margin: 10% 0;
	box-shadow: -11.31px 11.31px 25px 0px rgba(138, 131, 124, 0.23);
}

.history-info {
	width: 50%;
	position: absolute;
	top: 60px;
	left: 52px;
}

.history-content {
	width: 50%;
	position: absolute;
	top: 60px;
	right: 52px;
}

.history-content ol {
	padding: 20px;
}

.history-content li {
	margin: 5px 0;
}

.history-info p {
	margin: 10px 0;
}

.history-info .date {
	font: normal normal bold 20px/1.4em poppins-semibold, poppins, sans-serif;
	color: #0050ff;
}

.project-container {
	background-color: #e6dace;
	height: 100%;
	padding: 1%;
	text-align: center;
	margin-top: 10vh;
	position: relative;
}

.project-container h1 {
	margin: 50px 0;
	font: normal normal bold 35px/1.4em poppins-semibold, poppins, sans-serif;
}

.project-container h1::before {
	content: '';
	width: 16px;
	height: 16px;
	background-color: #0050ff;
	position: absolute;
	left: 43%;
	top: 9%;
	-webkit-transform: translateY(-20%);
	        transform: translateY(-20%);
}

.card-container {
	background-color: #e6dace;
	display: -webkit-flex;
	display: flex;
	-webkit-align-content: center;
	        align-content: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}

.card {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	width: 20%;
	margin: 1%;
	position: relative;
	cursor: pointer;
}

.card .active {
	-webkit-transform: rotateY(360deg);
	        transform: rotateY(360deg);
}

.card span {
	color: #fff;
	background-color: #4c5364;
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;
	width: 100%;
	padding: 2%;
}

.card img {
	width: 100%;
	height: 100%;
}

.card-content {
	opacity: 0;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(1, 1, 1, 0.8);
	color: #fff;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	text-align: center;
	transition: opacity 0.3s ease-in;
}

.card-content:hover {
	opacity: 0.98;
}

.card-content a {
	text-decoration: none;
	background-color: #0050ff;
	color: #fff;
	font-size: 15px;
	font-weight: 500;
	text-align: center;
	text-transform: capitalize;
	cursor: pointer;
	color: #fff;
	padding: 10px 20px;
	margin-top: 15%;
	border-radius: 2px;
	transition: all 0.5s ease 0s;
}

.project-container .arrow {
	position: absolute;
	background-color: transparent;
	border: 2px solid transparent;
	color: #0050ff;
	padding: 20px;
	font-size: 30px;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	cursor: pointer;
}

.project-container .arrow:disabled {
	display: none;
}

.project-container .arrow:focus {
	outline: 0;
}

.project-container .left-arrow {
	left: 1vw;
}

.project-container .right-arrow {
	right: 1vw;
}

.main {
	height: 100%;
}

.footer {
	display: -webkit-flex;
	display: flex;
	width: 100%;
	height: 10vh;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	background-color: #fff;
	position: relative;
}

.footer-copy {
	margin-left: 5%;
}

.footer-copy a {
	color: #000;
}

.footer-info {
	display: -webkit-flex;
	display: flex;
	width: 20%;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	-webkit-align-items: baseline;
	        align-items: baseline;
	text-align: center;
}
.footer-title {
	font-weight: bold;
	margin-bottom: 10px;
}

.footer-social i {
	color: #000;
	font-size: 19px;
	margin: 0 10px;
	cursor: pointer;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: Alata, sans-serif;
}

